import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew } from 'components/common/button';
import { CartHeadAction, TableData, ColEditDelete } from 'components/common/index';
import { Card, Row, Col, message } from 'antd';
import { getCandidates, deleteCandidate } from 'services/studentCandidate';
import FormSearchCandidate from './FormSearchCandidate';

class ListCandidateStudentPage extends Component {
  state = {
    reload: 0,
    paramSearch: {},
  };
  urlFormNew = '#/students/candidate/form/new';
  urlFormEdit = '#/students/candidate/form/edit';

  handleDelete = async id => {
    await deleteCandidate(id);
    message.success('Xa thnh cng');
    this.setState({ reload: this.state.reload + 1 });
  };

  handleSearch = values => {
    this.setState({ paramSearch: values });
  };

  render() {
    const columns = [
      {
        title: 'H tn',
        dataIndex: 'id',
        key: 'code',
        render: (text, record) => `${record.firstname} ${record.lastname}`,
      },
      {
        title: 'Ngy sinh',
        dataIndex: 'dob',
        key: 'dob',
      },
      {
        title: 'Gii tnh',
        dataIndex: 'genderText',
        key: 'genderText',
      },
      {
        title: 'HK',
        dataIndex: 'certType',
        key: 'certType',
      },
      {
        title: 'M trng',
        dataIndex: 'schoolId',
        key: 'schoolId',
      },
      {
        title: 'KV',
        dataIndex: 'areaId',
        key: 'areaId',
      },
      {
        title: 'Nm TN',
        dataIndex: 'graduatedYear',
        key: 'graduatedYear',
      },
      {
        title: 'H T',
        dataIndex: 'trainTypeId',
        key: 'trainTypeId',
      },
      {
        title: 'M ngnh',
        dataIndex: 'studyFieldId',
        key: 'studyFieldId',
      },
      {
        title: 'Khi',
        dataIndex: 'testGroup',
        key: 'testGroup',
      },
      {
        title: 'Mn 1',
        dataIndex: 'gradeM1',
        key: 'gradeM1',
      },
      {
        title: 'Mn 2',
        dataIndex: 'gradeM2',
        key: 'gradeM2',
      },
      {
        title: 'Mn 3',
        dataIndex: 'gradeM3',
        key: 'gradeM3',
      },
      {
        title: 'TC',
        dataIndex: 'totalGrade',
        key: 'totalGrade',
      },
      {
        title: 'Thao tc',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            hrefEdit={`${this.urlFormEdit}/${record.id}`}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    return (
      <Card title="">
        <CartHeadAction>
          <BtnAddNew href={this.urlFormNew} />
        </CartHeadAction>
        <FormSearchCandidate onSubmit={this.handleSearch} />
        <Row>
          <Col span={24}>
            <TableData
              reload={this.state.reload}
              paramSearch={this.state.paramSearch}
              service={getCandidates}
              columns={columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListCandidateStudentPage />
  </Page>
);



